.appWrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  background-color:grey;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  -ms-flex-pack: justify;
      justify-content: space-between;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.muiCard {
  width: 190px;
  height: 220px;
  margin: 5px; 
  position: relative;
}


.cardbutton {
  z-index: 21;
  padding: 0px;
  height: 10px;
}

.cardActions {
  padding: 0;
  text-align: center;
}

.cardMedia {
  width: 120px;
  height: 144px;
  /*padding-top: calc(363/302*100%)*/
}

.footerWrapper {

}

.contentWrapper {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  overflow-y: auto;
}

body {
  background-color: black;
  height:100%; /* force the BODY element to match the height of the HTML element */
}

html{
  min-height:100%;/* make sure it is at least as tall as the viewport */
  position:relative;
}


.main {
  width: 800px;
  height: 600px;
  background-color: black;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



